import { useCallback, useMemo, useState } from "react";
import { backBear, frontBear } from "../../../assets/images";
import { Button } from "../../../components";
import CheckBox from "../../../components/checkbox/checkbox";
import style from "./view-card.module.scss";

export const ViewPlanCard = (props: any) => {
  const { isActive, data, value, onClick } = props;

  const [selected, setSelected] = useState({
    Practice: true,
    Tests: true,
  });

  const onChange = useCallback((value: string, checked: boolean) => {
    setSelected((prev) => ({ ...prev, [value]: checked }));
  }, []);

  const total = useMemo(() => {
    let sum = 0;
    if (selected.Practice) sum += data?.pracAmt;
    if (selected.Tests) sum += data?.testAmt; 
    return sum;
  }, [data?.pracAmt, data?.testAmt, selected.Practice, selected.Tests]);

  const SubCard = useCallback(
    (title: "Practice" | "Tests", item: string[]) => (
      <div className={style.subCard}>
        <div className={style.title}>
          <CheckBox
            label={title}
            isSelected={selected[title]}
            value={title}
            onChange={onChange}
          />
        </div>
        <hr className={style.hrLine} />
        <div className="d-center d-col">
          {item.map((a) => (
            <div className={style.subText}>{a}</div>
          ))}
        </div>
      </div>
    ),
    [onChange, selected]
  );

  return (
    <div
      className={`${style.container} ${isActive ? style.active : ""}`}
      onClick={onClick}
    >
      {isActive ? <span className={style.checkbox}><CheckBox isRadio={true} isSelected={isActive} value={value} /></span>  : null }
      <img src={isActive ? frontBear : backBear} alt="bear" />
      <div className={style.head}>{data?.title}</div>
      {SubCard("Practice", data?.practice)}
      {SubCard("Tests", data?.tests)}
      <div className="d-center j-space-between w-100 mt-10">
        <div className={style.more}>View more info</div>
        <div className={style.amount}><>{total}$</></div>
      </div>
      {isActive ? (
        <Button className={`mt-10 ${style.regBtn}`}>Register now</Button>
      ) : null}
    </div>
  );
};
